	module.exports = {"data":{"site":{"siteMetadata":{"title":"weiran.co","author":"Weiran Zhang"}},"markdownRemark":{"id":"/Users/weiran/git/weiran/weiran.co/src/pages/blog/2014/5/why-does-apple-want-to-buy-beats/index.md absPath of file >>> MarkdownRemark","html":"<p>Apple’s interest in Beats Electronics got me thinking about their headphones. I know Apple is probably buying Beats for their music streaming rather than their hardware, but one doesn’t come with the other and the most iconic product of Beats are their headphones.</p>\n<p>A glance at your Twitter feed today and you would’ve seen endless comments comparing Beats customers to uninformed sheep willing to make Dr Dre rich by buying his ridiculously oversized yet mediocre sounding headphones. After that, theres the inevitable link to some headphones half the price and twice as good.</p>\n<p>To an extent I agree with them, Beats headphones are overpriced, but I’m not sure anyone buying a pair is under the impression they’re getting a bargain, or even that they’re getting the best headphones for their money. They’re buying Beats headphones because they want them.</p>\n<p>What Jimmy Iovine achieved is to explode a niche industry into the mainstream. How many people did you see buying £250 heaphones before Beats? That is nothing short of a miracle. This makes me think the deeper reason Apple wants Beats Electronic, is because they want Jimmy Iovine.</p>\n<p>Yet somehow, in all this news about headphones and music streaming, they forgot about Iovine.</p>","frontmatter":{"title":"Why does Apple want to buy Beats?","date":"May 09, 2014","passthroughUrl":null}}},"pathContext":{"slug":"/blog/2014/5/why-does-apple-want-to-buy-beats/","previous":{"fields":{"slug":"/blog/2014/4/dot-net-notts/"},"frontmatter":{"title":"dot net notts","passthroughUrl":"http://dotnetnotts.co","type":null}},"next":{"fields":{"slug":"/blog/2014/7/pinboard-turns-five-years-old/"},"frontmatter":{"title":"Pinboard turns five years old","passthroughUrl":"https://blog.pinboard.in/2014/07/pinboard_turns_five/","type":null}}}}

